<template>
<!-- this is the child component -->
  <div class="wyr">
    <h2>Please make your choice!</h2>
    <h3>{{ question }}</h3>
    <input type="radio" v-model="choice" v-bind:value="answer1" v-on:change="choiceMade"><label>{{ answer1 }}</label>
    <input type="radio" v-model="choice" v-bind:value="answer2" v-on:change="choiceMade"><label>{{ answer2 }}</label>
  </div>
</template>

<script>
export default {
  name: 'WouldYouRather',
  props: {
    question: String,
    answer1: String,
    answer2: String
    //props are for data given to child by parent, component should not modify props
  },
  data() {
    return {
      choice:""
      //choice will update in devtools when radio buttons are selected
      //data function is for data that is used internally to the component
    }
  },
  methods: {
    choiceMade() {
      //emit an event 'answer-changed' with value of selection
      this.$emit('answer-changed', this.choice)
      //just because the syntax isn't already complex enough, now we have $
      // name of event is 'answer-changed', convention is for dashes in events
      // $emit(name-of-event, argument1, argument2, ..)
    }
  }
}
//parent component is App.vue and it will provide the values for the above props
//devtools will show props for this file and data for App.vue
</script>

